import {NavLink} from "react-router-dom";
import Lottie from "react-lottie";
const Landing=(props)=>{
  const {brand,para,btnPath,btText,jsnfile}=props;
  const defaultOptions = {
      loop: true,
      autoplay: true, 
      animationData:jsnfile,
      isClickToPauseDisabled:true,
      rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice'
      }
    };
  return (
    <>
      <div className="container">
        <div className="row">
          <div className="col-md-6 order-md-2 d-flex justify-content-center">
            <figure className="PageLogo p-5" >
            <Lottie 
            options={defaultOptions}
            />
            </figure>
          </div>
          <div className="col-md-6 order-md-1 p-5 d-flex flex-column justify-content-center align-items-start ">
            <h1 className="BrandName">{brand}</h1>
            <p>{para}</p>
            <NavLink className="btn btn-outline-primary" to={btnPath}>{btText}</NavLink>
          </div>
        </div>
      </div>
    </>
    )
}
  
export default Landing;