const ProgressBar=(props)=>{
    const Barstyle={
      "width":props.val+"%",
      "backgroundColor":props.color,
    }
    return(
    <>
    <label className="form-label mt-2" htmlFor="#progresbar">{props.name}</label>
      <div className="progress" id="progresbar">
        <div className="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" aria-valuenow={props.val} aria-valuemin="0" aria-valuemax="100" style={Barstyle}></div>
      </div>
    </>
      )
  }
  export default ProgressBar;